<template>
  <div class="l-page">
    <v-nav :title="$t('common.payment.title')"/>
    <div class="l-page__content has-padding--around">
      <div class="is-tip--dark">{{ $t('common.payment.tip') }}</div>
      <div
        v-for="(type, index) in types"
        :key="type.key"
        class="o-payment has-padding--around has-shadow u-separate l-flex--row is-centered"
        :class="{ 'is-selected': selectedIndex === index }"
        @click="selectedIndex = index"
      >
        <div class="o-payment__icon l-flex__none">
          <van-icon name="wechat"/>
        </div>
        <div class="is-content--dark u-font u-h-separate--farthest l-flex__fill">{{ $t(`common.payment.${ type.key }`) }}</div>
        <van-icon
          v-if="selectedIndex === index"
          class="u-color--blue u-h-separate--near"
          name="checked"
        />
      </div>
    </div>
    <div class="c-fixed-footer has-hairline--tb">
      <v-button
        class="l-flex__fill"
        :text="$t('btn.confirmupper')"
        :disable="selectedIndex === -1"
        small
        @click="toPay"
      />
    </div>
    <div
      v-if="paying"
      class="c-processing is-fill l-flex--column is-centered"
    >
      <van-loading class="u-color--white" size="36"/>
      <div class="is-tip u-color--white u-font--bold u-separate--near">{{ $t('common.payment.processing') }}</div>
    </div>
  </div>
</template>

<script>
import { Icon, Loading } from 'vant'
import VButton from '@/components/VButton.vue'

export default {
  components: {
    [ Icon.name ]: Icon,
    [ Loading.name ]: Loading,
    VButton
  },
  data () {
    return {
      selectedIndex: -1,
      types: [
        {
          key: 'wechat'
        },
        {
          key: 'visa'
        },
        {
          key: 'card'
        },
        {
          key: 'jcb'
        },
        {
          key: 'amex'
        },
        {
          key: 'alipay'
        }
      ],
      paying: false
    }
  },
  methods: {
    toPay () {
      this.paying = true
      setTimeout(() => {
        this.$router.replace({
          name: 'payment-result',
          params: {
            id: this.$route.params.id
          },
          query: {
            status: 'payed'
          }
        })
      }, 2000)
    }
  }
}
</script>

<style lang="scss" scoped>
.o-payment {
  position: relative;
  border-radius: 16px;

  &.is-selected::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border: 4px solid $color-blue;
    border-radius: 16px;
  }

  &__icon {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 186px;
  }
}

.c-processing {
  font-size: 60px;
  background-color: rgba($color-black, .67);
  z-index: 99;
}
</style>
